const app = getApp().globalData;
const { navBar } = app;
export default {
	data() {
		return {
			address: '',
			navBar: navBar,
			isactive:true
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},

		mapPage() {
			let that = this;

			uni.chooseLocation({
				success: function(res) {
					that.address = res.address;
				},
				fail: function(err) {
					console.log(err);
				}
			});
		}
	}
};
